'use client';

import React, { useEffect, useState } from 'react';
import { supabase } from '@/lib/supabase';
import { useRouter } from 'next/navigation';
import { ShieldCheck, Key, History, Download, Loader2, LogOut, CheckCircle2, User } from 'lucide-react';

export default function DashboardPage() {
  const router = useRouter();
  const [loading, setLoading] = useState(true);
  const [saving, setSaving] = useState(false);
  const [message, setMessage] = useState<{ type: 'success' | 'error', text: string } | null>(null);
  
  // State untuk menyimpan data kunci API
  const [profile, setProfile] = useState({
    full_name: '',
    openai_key: '',
    anthropic_key: '',
    gemini_key: '',
    mistral_key: '',
    llama_key: '',
    is_pro: false
  });

  // 1. Ambil data profil saat halaman dimuat
  useEffect(() => {
    const fetchProfile = async () => {
      const { data: { user } } = await supabase.auth.getUser();
      
      if (!user) {
        router.push('/auth/login');
        return;
      }

      const { data, error } = await supabase
        .from('profiles')
        .select('*')
        .eq('id', user.id)
        .single();

      if (data) {
        setProfile({
          full_name: data.full_name || '',
          openai_key: data.openai_key || '',
          anthropic_key: data.anthropic_key || '',
          gemini_key: data.gemini_key || '',
          mistral_key: data.mistral_key || '',
          llama_key: data.llama_key || '',
          is_pro: data.is_pro || false
        });
      }
      setLoading(false);
    };

    fetchProfile();
  }, [router]);

  // 2. Fungsi untuk menyimpan perubahan ke Supabase
  const handleUpdateKeys = async () => {
    setSaving(true);
    setMessage(null);
    
    const { data: { user } } = await supabase.auth.getUser();
    
    if (!user) return;

    const { error } = await supabase
      .from('profiles')
      .update({
        openai_key: profile.openai_key,
        anthropic_key: profile.anthropic_key,
        gemini_key: profile.gemini_key,
        mistral_key: profile.mistral_key,
        llama_key: profile.llama_key,
        updated_at: new Date()
      })
      .eq('id', user.id);

    if (error) {
      setMessage({ type: 'error', text: 'Gagal memperbarui kunci: ' + error.message });
    } else {
      setMessage({ type: 'success', text: 'Intelligence Vault berhasil diperbarui secara aman.' });
      setTimeout(() => setMessage(null), 3000);
    }
    setSaving(false);
  };

  const handleSignOut = async () => {
    await supabase.auth.signOut();
    router.push('/');
  };

  if (loading) {
    return (
      <div className="min-h-screen bg-[#FDFCFB] flex items-center justify-center">
        <Loader2 className="animate-spin text-[#D4AF37] w-12 h-12" />
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-[#FDFCFB] text-black pb-32 font-sans">
      
      {/* HEADER SECTION */}
      <section className="max-w-6xl mx-auto pt-24 pb-12 px-8">
        <div className="flex flex-col md:flex-row justify-between items-end gap-8">
          <div className="space-y-4">
            <div className={`inline-flex items-center gap-2 px-4 py-1.5 rounded-full text-[12px] font-black uppercase tracking-[0.3em] ${profile.is_pro ? 'bg-black text-[#D4AF37]' : 'bg-gray-100 text-gray-400'}`}>
              <ShieldCheck className="w-4 h-4" /> {profile.is_pro ? 'Verified Pro Account' : 'Standard Account'}
            </div>
            <h1 className="text-5xl font-serif italic text-black">Command <span className="text-[#D4AF37]">Center.</span></h1>
            <p className="text-[16px] text-gray-400 font-light italic">Welcome back, {profile.full_name}. Manage your elite fiscal intelligence assets.</p>
          </div>
          <div className="flex gap-4">
            <button onClick={handleSignOut} className="px-8 py-3 bg-white border border-gray-100 rounded-full text-[14px] font-black uppercase tracking-widest hover:text-red-500 transition-all flex items-center gap-3">
              <LogOut size={16} /> Sign Out
            </button>
          </div>
        </div>
      </section>

      {/* API VAULT SECTION */}
      <section className="max-w-6xl mx-auto px-8 mb-20">
        <div className="bg-white border border-gray-100 rounded-[3.5rem] p-12 shadow-2xl shadow-gray-100/50 relative overflow-hidden">
          <div className="flex items-center justify-between mb-12 border-b border-gray-50 pb-8">
            <div className="flex items-center gap-4">
              <Key className="text-[#D4AF37]" />
              <h2 className="text-[14px] font-black uppercase tracking-[0.4em]">Intelligence API Vault</h2>
            </div>
            {message && (
              <div className={`flex items-center gap-2 text-[14px] font-bold ${message.type === 'success' ? 'text-green-600' : 'text-red-500'}`}>
                {message.type === 'success' && <CheckCircle2 size={18} />} {message.text}
              </div>
            )}
          </div>
          
          <div className="grid md:grid-cols-2 lg:grid-cols-3 gap-8">
            {[
              { id: 'openai_key', label: 'OpenAI', model: 'GPT-5.2 / o3' },
              { id: 'anthropic_key', label: 'Anthropic', model: 'Claude 4.5 Opus' },
              { id: 'gemini_key', label: 'Google Gemini', model: 'Gemini 3 Ultra' },
              { id: 'mistral_key', label: 'Mistral AI', model: 'Mistral Large 3' },
              { id: 'llama_key', label: 'Meta Llama', model: 'Llama 4 (70B)' }
            ].map((api) => (
              <div key={api.id} className="p-8 bg-[#FDFCFB] rounded-3xl border border-gray-50 space-y-6 hover:border-[#D4AF37]/30 transition-all">
                <div className="flex justify-between items-start">
                  <span className="text-[14px] font-black uppercase tracking-widest text-black">{api.label}</span>
                  <div className={`px-3 py-1 rounded-full text-[9px] font-black uppercase tracking-widest ${(profile as any)[api.id] ? 'bg-green-50 text-green-600' : 'bg-gray-100 text-gray-400'}`}>
                    {(profile as any)[api.id] ? 'Active' : 'Empty'}
                  </div>
                </div>
                <div className="space-y-4">
                  <p className="text-[12px] text-gray-400 uppercase tracking-widest">Model: <span className="text-black font-bold">{api.model}</span></p>
                  <input 
                    type="password" 
                    value={(profile as any)[api.id]}
                    onChange={(e) => setProfile({ ...profile, [api.id]: e.target.value })}
                    placeholder="sk-••••••••••••"
                    className="w-full bg-white border border-gray-100 rounded-xl py-3.5 px-4 text-[14px] focus:outline-none focus:border-[#D4AF37] transition-all"
                  />
                </div>
              </div>
            ))}
          </div>

          <div className="mt-16 flex justify-end">
             <button 
               onClick={handleUpdateKeys}
               disabled={saving}
               className="px-14 py-6 bg-black text-[#D4AF37] rounded-2xl text-[14px] font-black uppercase tracking-[0.3em] hover:bg-[#D4AF37] hover:text-black transition-all shadow-xl flex items-center gap-4 disabled:opacity-50"
             >
               {saving ? <Loader2 className="animate-spin" /> : 'Update All Vault Keys'}
             </button>
          </div>
        </div>
      </section>

      {/* ARCHIVE SECTION */}
      <section className="max-w-6xl mx-auto px-8">
        <div className="bg-white border border-gray-100 rounded-[3.5rem] p-12 shadow-sm">
          <div className="flex items-center gap-4 mb-12">
            <History className="text-[#D4AF37]" />
            <h2 className="text-[14px] font-black uppercase tracking-[0.4em]">Recent Analysis Archive</h2>
          </div>
          
          <div className="text-center py-20 border-2 border-dashed border-gray-50 rounded-[2rem]">
             <p className="text-[16px] text-gray-400 italic">No reports generated yet. Launch the lab to begin.</p>
          </div>
        </div>
      </section>
    </div>
  );
}